import React, { Component } from 'react';
import { Formik, FormikProps, Form, Field, ErrorMessage } from 'formik';
import {updaterole, updateRole,getRole, getHospitalSet} from './../Apis.js'
import { Alert } from 'reactstrap';
import * as Yup from 'yup';
import {
  Badge,
  Button,
  ButtonDropdown,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';



class UpdateRole extends Component {
    constructor(props) {
        super(props);
        this.state = {
           
            visible:false,
            
            showForm:false,
            msgType:'success',
            massege:''
          
        };
        this.onDismiss = this.onDismiss.bind(this);
      }
    onDismiss() {
        this.setState({ visible: false });
      }
    handleSubmit = (values, { 
        props = this.props, 
        setSubmitting 
      }) => {
       
        console.log(values);
        this.updateRole(values,setSubmitting,this.props.match.params.id)
  
      }
    
     
    
      updateRole=async(values,setSubmitting,id)=>{
            const responseData= await updateRole(values,id);
            console.log(responseData,'response')
            if(responseData.data.message)
            {
                this.setState({ visible:true,massege:'Role Update Failed '+responseData.message,msgType:'danger'})
                setSubmitting(false);
            }
            else{
                this.setState({ visible:true,msgType:'success',massege:'Roles Update Successfully'})
                setSubmitting(false);
                return;
             
            }
        }
         
        

       
        
        
        componentDidMount =async()=>{
            const responseData= await getHospitalSet();
            console.log(responseData,'response')
            if(responseData.status == 200){
                this.setState({hospitalSets:responseData.data.result})
            } else if (responseData.status == 404) {
                console.warn(responseData.data.message);
            }
            const responseDataRole= await getRole(this.props.match.params.id);
            console.log(responseDataRole,'response a')
            if(responseDataRole.status==200)
            {
                this.setState({
                    name: responseDataRole.data.name,
                    hospital_id: responseDataRole.data.hospital_id,
                    showForm:true
                })
            }
            else{
               console.log(responseDataRole)
            }
       


        }
    render() {
        console.log(this.state.name)
       
        return(
            <div className="animated fadeIn">
                <Row>
                <Col xs="2">
                </Col>
                    <Col xs="8">
                    <Card>
                        <CardHeader>
                            <strong>Update role</strong>
                        </CardHeader>
                        <CardBody> 
                            {this.state.showForm&&<Formik
                                    initialValues={{
                                        name: this.state.name,
                                        hospital_id: this.state.hospital_id,
                                      
                                    }}
                            /*validate={values => {
                                let errors = {};
                                if (!values.email) {
                                  errors.email = 'Required';
                                } else if (
                                  !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                                ) {
                                  errors.email = 'Invalid email address';
                                }
                                return errors;
                              }}*/
                            //   validationSchema={Yup.object().shape({
                            //     email: Yup
                            //         .string()
                            //         .email()
                            //         .required(),
                        
                            //         secondary_phone:Yup
                            //         .number()
                            //         .required(),
                            //         latitude:Yup
                            //         .number()
                            //         .required(),
                            //         longitude:Yup
                            //         .number()
                            //         .required(),
                            //         primary_phone:Yup
                            //         .number()
                            //         .required(),
                            //     })}
                            
                                    onSubmit={this.handleSubmit}
                                    render={formProps => {
                                        return(
                                            <Form className="form-horizontal">
                                  <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                            type="text" 
                                            name="name" 
                                            placeholder="Name" 
                                            id="text-input"
                                            className="form-control"
                                            /> 
                                           
                                        </Col>                                        
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Hospital Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                    
                                            <Field 
                                           component="select" 
                                            name="hospital_id" 
                                            placeholder="Hospital Id" 
                                            className="form-control"
                                            >
                                            {this.state.hospitalSets&&this.state.hospitalSets.map((hospital, index) =>
                                            <option key={index} value={hospital.id}>{hospital.name}</option>
                                            )}
                                            </Field> 
                                            <ErrorMessage name="short_name" />
                                        </Col>                                        
                                    </FormGroup>
                                            <Col md="3" style={{float:'right'}}>
                                            <button 
                                                type="submit"
                                                className="btn btn-success btn-block custonbtn" 
                                                
                                                disabled={formProps.isSubmitting}>
                                               Update role
                                            </button>
                                            </Col>
                                            </Form>
                                );
                            }}

                            />}
                             <Alert color={this.state.msgType} style={{marginTop:65}}isOpen={this.state.visible} toggle={this.onDismiss}>
                                {this.state.massege}
                            </Alert>
                        </CardBody>
                    </Card>
                    </Col>
                </Row>
            </div>
            
        );
    }

}

export default UpdateRole;